@font-face {
	font-family: 'Spartan'; 
	src: url('../../shrifts/Spartan.ttf');
}
.AppHeader{
    background: #3C2AFA;
    width: 100%;
    height: 1.0em;
    font-family: Spartan;
    font-style: normal;
    font-weight: normal;
    font-size: 40px;
    padding-bottom: 3%;
    line-height: 67px;
    color: #FFFFFF;
    padding-top: 1%;
    padding-left: 45%;
    position: relative;
    z-index: 6;
    display: flex;
    flex-direction: row;
}
.burger{
    width: 75px;
    height: 75px;
    margin-top: 0%;
    margin-right: 15%;
    
}
:hover.burger{
    cursor: pointer;
    width: 76px;
    height: 76px;
}